import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {RusService} from './rus.service';
import {EngService} from './eng.service';

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  rusArr: any[] = [];
  engArr: any[] = [];
  word: '';
  lang = 'rus';
  list: any[] = [];
  wordBody: any;
  listTime = [
    {
      eng: 'Future question',
      rus: 'Будущее вопрос',
      arr: [],
      code: 'futQ',
    },
    {
      eng: 'Future statement',
      rus: 'Будущее утверждение',
      arr: [],
      code: 'futS',
    },
    {
      eng: 'Future denial',
      rus: 'Будущее отрицание',
      arr: [],
      code: 'futN',
    },
    {
      eng: 'Simple question',
      rus: 'Настоящее вопрос',
      arr: [],
      code: 'presQ',
    },
    {
      eng: 'Simple statement',
      rus: 'Настоящее утверждение',
      arr: [],
      code: 'presS',
    },
    {
      eng: 'Simple denial',
      rus: 'Настоящее отрицание',
      arr: [],
      code: 'presN',
    },
    {
      eng: 'Past question',
      rus: 'Прошедшее вопрос',
      arr: [],
      code: 'pastQ',
    },
    {
      eng: 'Past statement',
      rus: 'Прошедшее утверждение',
      arr: [],
      code: 'pastS',
    },
    {
      eng: 'Past denial',
      rus: 'Прошедшее отрицание',
      arr: [],
      code: 'pastN',
    },
  ];

  constructor(private  router: Router, private rusService: RusService, private engService: EngService ) { }

  selItem(type, body): any {
    this[type] = body;
  }

  onChange(event): any {
    this.word = event;
    for ( let i = 0 ; i < this.list.length; i++) {
      if ( this.word === this.list[i][this.lang]) {
        this.wordBody = this.doBody(this.list[i]);
      }
    }
  }

  doBody(body): any {
    let bb = this.listTime;
    for (let i = 0; i < bb.length; i++) {
      bb[i].arr = [];
      for (let j = 1; j < 7; j++) {
        bb[i].arr.push({
          eng: body.engArr[bb[i].code + j],
          rus: body.rusArr[bb[i].code + j],
        });
      }
    }
    console.log(bb);
    return bb;
  }

  doString(str): any {
    let newStr = str.split(' ');
    let newStr1 = newStr[newStr.length - 1];
    // console.log(newStr1);
    let newStr2 = '';
    if ( newStr1[newStr1.length - 1] === '?' ) {
      newStr2 = newStr[newStr.length - 1].slice(0, newStr[newStr.length - 1].length - 1);
      return newStr2;
    } else {
      newStr2 = newStr[newStr.length - 1];
      return newStr2;
    }
  }

  createBase(): any {
    this.rusArr = this.rusService.data;
    this.engArr = this.engService.data;
    for ( let i = 0; i < this.rusArr.length; i++) {
      for ( let j = 0; j < this.engArr.length; j++) {
        if (this.rusArr[i] === this.rusArr[j]) {
          this.list.push({
            eng: this.doString(this.engArr[j].futQ1),
            rus: this.doString(this.rusArr[i].futQ1),
            engArr: this.engArr[j],
            rusArr: this.rusArr[i],
          });
        }
      }
    }
  }

  ngOnInit(): void {
    this.createBase();
  }

}
